import React, { Component } from 'react'
import { Route, withRouter } from 'react-router-dom'
import PropTypes from 'prop-types'
import _some from 'lodash/some'
import './App.css'
import { Header, Footer } from './components'
import IndexPage from './containers/IndexPage'
import YearbookPage from './containers/YearbookPage'
import ContactUSPage from './containers/ContactUsPage'
import AboutPage from './containers/AboutPage'

const FORCE_CHANGE_HEADER = ['/yearbook', '/contact', '/about']
class App extends Component {
  static propTypes = {
    location: PropTypes.object.isRequired,
  }

  state = {
    forceChange: false,
  }

  componentDidMount() {
    if (_some(FORCE_CHANGE_HEADER, item => item === this.props.location.pathname)) {
      this.setState({ forceChange: true })
    } else {
      this.setState({ forceChange: false })
    }
  }

  componentDidUpdate(prevProps) {
    if (this.props.location !== prevProps.location) {
      if (_some(FORCE_CHANGE_HEADER, item => item === this.props.location.pathname)) {
        this.setState({ forceChange: true })
      } else {
        this.setState({ forceChange: false })
      }
    }
  }
  render() {
    const { forceChange } = this.state
    return (
      <div style={{ minHeight: '100vh' }}>
        <Header forceChange={forceChange} />
        <Route exact path="/" component={IndexPage} />
        <Route exact path="/register" render={props => <IndexPage {...props} isShowRegister />} />
        <Route path="/about" component={AboutPage} />
        <Route path="/yearbook" component={YearbookPage} />
        <Route path="/contact" component={ContactUSPage} />
        <Footer />
      </div>
    )
  }
}

export default withRouter(App)
